<template>
  <div id="Agendar">
    <a href= "/Request">Regresar al menú de temas</a>
    <br>
    <h4>SECCIÓN 3</h4>
    <p>Proporciona mayor información sobre tu solicitud</p>

    <div class="Textarea">
      <p>1. Por favor escribe tu solicitud de maneta detallada:</p>
      <b-form-textarea
        id="textarea"
        v-model="text"
        placeholder="Escribe algo..."
        rows="3"
        max-rows="6"
      ></b-form-textarea>

      <pre class="mt-3 mb-0">{{ text }}</pre>
    </div>

    <div class="Select">
      <p>2. Por favor selecciona el área a la que pertenece tu solicitud:</p>
      <b-form-select 
        v-model="selected" 
        :options="options" 
        size="md" 
        class="mt-3"
      >
      </b-form-select>
    </div>

    <br>

    <div class="Calendario">
      <p>3. En caso de requerir una reunión para resolver tu solicitud, por favor selecciona una fecha
        disponible en el calendario:
      </p>
      <b-calendar 
        v-model="value" 
        :date-disabled-fn="dateDisabled" 
        block locale="es-LA"
        selected-variant="danger"
        today-variant="info"
        class="border rounded p-2"
        >
      </b-calendar>
    </div>

    <br>

    <div class="Hora">
      <p>4. Selecciona la hora a la que quieres la reunión:
      </p>
      <b-form-select
      v-model="selectedhour"
      :options="optionshour"
      class="mb-3"
      value-field="item"
      text-field="name"
      disabled-field="notEnabled"
      ></b-form-select>
    </div>

    <br>
    
    <center>
    <b-button type="submit" variant="primary">Enviar</b-button>
    <a href= "/evaluar">Danos tu retroalimentación!</a>
    </center>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        selected: null,
        options: [
          { value: null, text: 'Selecciona una opción' },
          { value: 'a', text: 'Innovación' },
          { value: 'b', text: 'Política' },
          { value: { C: '3PO' }, text: 'Fortalecimiento' },
          { value: 'd', text: 'Jurídica' }
        ],
        value: '',

        selectedhour: 'A',
        optionshour: [
          { item: 'A', name: '9:00a.m - 10:00a.m' },
          { item: 'B', name: '10:00a.m - 11:00a.m' },
          { item: 'D', name: '11:00a.m - 12:00a.m', notEnabled: true },
          { item: { d: 1 }, name: '2:00p.m - 3:00p.m' }
        ]
      }
    },
    methods: {
      dateDisabled(ymd, date) {
        // Disable weekends (Sunday = `0`, Saturday = `6`) and
        // disable days that fall on the 13th of the month
        const weekday = date.getDay()
        const day = date.getDate()
        // Return `true` if the date should be disabled
        return weekday === 0 || weekday === 6 || day === 13
      }
    }
  }
</script>

<style>
#Agendar h4,p {
  text-align: center;
}
#Agendar a {
  margin-left: 10%;
}
.Textarea {
  margin-left:10%;
  margin-right:10%;
}
.Textarea p {
  text-align: left;
}
.Select {
  margin-left:10%;
  text-align: left;
}
.Select p {
  text-align: left;
}
.Calendario {
  margin-left:10%;
  margin-right:10%;
  text-align: left;
}
.Calendario p {
  text-align: left;
}
.Hora {
  margin-left:10%;
  margin-right:10%;
  text-align: left;
}
.Hora p {
  text-align: left;
}
</style>


