<template>
  <div id="Evaluacion">
        <div class="Encabezado">
            <h4>RETROALIMENTACIÓN</h4>
            <p>Tu opinión es muy importante para nosotros</p>
        </div>

        <div class="Pregunta">
            <p>¿Esta herramienta te ayudó a resolver tu solicitud?</p>
                <b-form-group
                    v-slot="{ ariaDescribedby }"
                >
                    <b-form-checkbox
                        v-for="option in options"
                        v-model="selected"
                        :key="option.value"
                        :value="option.value"
                        :aria-describedby="ariaDescribedby"
                        name="flavour-4a"
                        inline
                    >
                        {{ option.text }}
                    </b-form-checkbox>
                </b-form-group>
        </div>
        <br>
        <div class="Rating">
            <p> Por favor califica qué tanto te ayudó esta herramienta a resolver tu solicitud</p>
            <b-form-rating v-model="value" variant="warning" class="mb-2"></b-form-rating>
        </div>
        <br>
        <center>
        <b-button type="submit" variant="primary">Finalizar</b-button>
        </center>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        value: 0,
        selected: [],
        options: [
            { text: 'Si', value: 'Si' },
            { text: 'No', value: 'No' }
        ]      
        }
    }
  }
</script>

<style>
#Evaluacion {
    margin-top: 2%;
    margin-left: 10%;
    margin-right: 10%;
    text-align: left;
}
#Pregunta checkbox {
    text-align: left;
}
.Encabezado {
    text-align: center;
}
#button {
    text-align: center;
}
</style>


