import vueRouter from 'vue-router'

import App from '../App'
import Home from '../components/Home'
import Personas from '../components/Personas'
import Request from '../components/Request'
import Lineamientos from '../components/Lineamientos'
import Socializacion from '../components/Socializacion'
import Acompanamiento from '../components/Acompanamiento'
import Fortalecimiento from '../components/Fortalecimiento'
import Medicion from '../components/Medicion'
import Agendar from '../components/Agendar'
import Evaluacion from '../components/Evaluacion'

const router = new vueRouter({
    mode: 'history',
    base: __dirname,
    routes: [
        {
            path: '/',
            name: "root",
            component: Home
            },
        {
            path: '/home',
            name: "Home",
            component: Home
        },
        {
            path: '/personas',
            name: "Personas",
            component: Personas
        },
        {
            path: '/request',
            name: "Request",
            component: Request
        },
        {
            path: '/lineamientos',
            name: "Lineamientos",
            component: Lineamientos
        },
        {
            path: '/socializacion',
            name: "Socializacion",
            component: Socializacion
        },
        {
            path: '/acompanamiento',
            name: "Acompanamiento",
            component: Acompanamiento
        },
        {
            path: '/fortalecimiento',
            name: "Fortalecimiento",
            component: Fortalecimiento
        },
        {
            path: '/medicion',
            name: "Medicion",
            component: Medicion
        },
        {
            path: '/agendar',
            name: "Agendar",
            component: Agendar
        },
        {
            path: '/evaluar',
            name: "Evaluacion",
            component: Evaluacion
        }
        ]
        })
        export default router


