<template>
  <div id="Personas">
    <h4>SECCIÓN 1. DATOS PERSONALES</h4>
    <p>Por favor diligencia tus datos para conocerte mejor</p>

    <b-form @submit="onSubmit" @reset="onReset" v-if="show">

        <b-form-group 
            id="input-group-1" 
            label="Nombres:" 
            label-for="input-1"
            >
            <b-form-input
            id="nombre_pers"
            v-model="form.name"
            placeholder="Por favor escribe tu(s) nombre(s)"
            required
            ></b-form-input>
        </b-form-group>
        <br>
        <b-form-group 
            id="input-group-1" 
            label="Apellidos:" 
            label-for="input-1"
            >
            <b-form-input
            id="apellido_pers"
            v-model="form.lastname"
            placeholder="Por favor escribe tus apellidos"
            required
            ></b-form-input>
        </b-form-group>
        <br>
        <b-form-group
            id="input-group-2"
            label="Correo electrónico:"
            label-for="input-2"
            
            >
            <b-form-input
            id="correo_pers"
            v-model="form.email"
            type="email"
            placeholder="Escribe tu correo electrónico"
            required
            ></b-form-input>
        </b-form-group>
        <br>
        <b-form-group 
            id="input-group-3" 
            label="Entidad:" 
            label-for="input-3"
            >
            <b-form-input
            id="entidad_pers"
            v-model="form.entidad"
            placeholder="Por favor escribe el nombre de la entidad a la que perteneces"
            required
            ></b-form-input>
        </b-form-group>
        <br>
        <b-form-group 
            id="input-group-4" 
            label="Cargo:" 
            label-for="input-4"
            >
            <b-form-input
            id="cargo_pers"
            v-model="form.cargo"
            placeholder="Por favor escribe tu cargo"
            required
            ></b-form-input>
        </b-form-group>
        <br>
        <b-form-group 
            id="input-group-5" 
            label="Departamento:" 
            label-for="input-5"
            >
            <b-form-select
                id="departamento_pers"
                v-model="form.departamento"
                :options="departamentos"
                style="width: 100%"
                required
            >
            </b-form-select>
        </b-form-group>
        <br>
        <b-form-group 
            id="input-group-5" 
            label="Municipio:" 
            label-for="input-5"
            >
            <b-form-select
                id="municipio_pers"
                v-model="form.municipio"
                :options="municipios"
                style="width: 100%"
                required
            >
            </b-form-select>
        </b-form-group>
        <br>
        <b-form-group 
            id="input-group-5" 
            label="Sector:" 
            label-for="input-5"
            >
            <b-form-select
                id="sector_pers"
                v-model="form.sector"
                :options="sectores"
                style="width: 100%"
                required
            >
            </b-form-select>
        </b-form-group>
        <br>
        <center>
            <button type="submit" class="btn btn-primary" v-on:click="createUsuario">Guardar</button>
            <b-button type="submit" variant="primary" href="/request">Siguiente</b-button>
        </center>
        
    </b-form>

    <center>
        <b-form-checkbox
