        createUsuario: function () {
            this.nombres = document.getElementById("nombre_pers").value
            this.apellidos = document.getElementById("apellido_pers").value
            this.correo = document.getElementById("correo_pers").value
            this.entidad = document.getElementById("entidad_pers").value
            this.cargo = document.getElementById("cargo_pers").value
            this.departamento = document.getElementById("departamento_pers").value
            this.municipio = document.getElementById("municipio_pers").value
            this.sector = document.getElementById("sector_pers").value
            
            this.newUsuario = {
                            "nombres": this.nombres,
                            "apellidos": this.apellidos,
                            "correo": this.correo,
                            "entidad": this.entidad,
                            "cargo": this.cargo,
                            "departamento": this.departamento,
                            "municipio": this.municipio,
                            "sector": this.sector,
            }
            console.log(this.newUsuario)   
            let self = this          
            axios.post("http://127.0.0.1:8000/usuario/crear/", this.newUsuario)
                .then((result) => {
